.to-do-list-form {
	display: flex;
	justify-content: space-between;
	gap: 10px;
	height: 100%;
}
.form__input {
	min-width: 130px;
	width: 80%;
	padding: 20px;
}
.form__button {
	min-width: 60px;
	font-size: 30px;
}
@media (min-width: 769px) {
	.form__button {
		cursor: pointer;
	}
}
